

.site-content_headline {
	max-width: 1100px;
	height: 100vh;
  margin-top: -50rem;
	margin-left: auto;
	margin-right: auto;
  margin-bottom: 20rem;
	display: flex;
	align-items: center;
	justify-content: center;
  font-weight: 200;
  color: #303030;
  font-size: calc(2vw + 25px);
}

.hero {
  background: rgb(255, 255, 255);
  .container {
    @media (min-width: 360px) {
      padding: 0 30px;
    }
    @media (min-width: 768px) {
      padding: 0 100px;
    }
    @media (min-width: 1024px) {
      padding: 0 120px;
    }
    @media (min-width: 1200px) {
      padding: 0 150px;
    }
    @media (min-width: 1350px) {
      padding: 0 180px;
    }
    @media (min-width: 1600px) {
      padding: 0 220px;
    }
    @media (min-width: 1800px) {
      padding: 0 270px;
    }

    .hero-inner {
      height: 100vh;
      display: flex;
      align-items: center;
      justify-self: center;
      .hero-inner-col {
        &.left {
          display: none;
        }
        &.right {
        }
        .hero-inner-title {
          h1 {
            margin: 0;
            font-weight: 400;
            @media (min-width: 1800px) {
              font-size: 28px;
            }
          }
        }
        .hero-inner-links {
          display: flex;
          .hero-inner-link-item {
            .hero-inner-link-item-padding {
              &:before {
                content: "";
                display: block;
                top: -30vh;
                bottom: -30vh;
                left: 0;
                right: 0;
              }
            }
            a {
              max-width: 1100px;
              margin-top: -370px;
              margin-left: auto;
              margin-right: auto;
              align-items: center;
              justify-content: center;
              font-weight: 200;
              color: #303030;
              border-radius: 10px;
              font-size: calc(2vw + 20px);
              padding: 12px 22px;
              text-decoration: none;
              color: transparent;
              transition: color 0.3s, -webkit-text-stroke 0.3s;
              -webkit-text-stroke: 1px #000;
              &:hover {
                color: black;
                -webkit-text-stroke: 1px transparent;
              }
              span {
                line-height: 1.1;
                font-weight: 700;
                text-shadow: none;
                font-size: 5.8vw;
              }
            }
            &:first-child {
              a {
                padding-left: 0;
              }
            }
            &:last-child {
              a {
                padding-right: 0;
              }
            }
          }
        }
      }
    }
    .hero-inner-footer {
      height: 100%;
      width: 100%;
      margin-left:  auto;
      margin-right: auto;
      background-color: #fafafa;
      .hero-inner-footer-text {
        p {
          max-width: 1100px;
          margin-top: -400px;
          margin-left: auto;
          margin-right: auto;
          display: flex;
          align-items: center;
          justify-content: center;
          font-weight: 200;
          color: #303030;
          border-radius: 10px;
          font-size: calc(2vw + 20px);
        }
      }
    }
  }

  .frameworks-section {
    .frameworks-text {
      p {
        max-width: 1100px;
        margin-top: auto;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 200;
        color: #303030;
        border-radius: 10px;
        font-size: calc(2vw + 20px);
      }
    }
  }

}